import axios from 'axios'
/* VARIBLE PARA ALMACENAR EL TOKEN DEL USUARIO ACTIVO */

let user = JSON.parse(localStorage.getItem('user'));

/* VERIFICA SI EL USUARIO ESTA ACTIVO,
*  REVISA QUE HAYAS DATOS EN EL JSON LOCAL
*
* SI HAY ES DECIR QUE ESTA ACTIVO, ASI QUE RETORNA SU TOKEN *
* EN CASO CONTRARIO, RETORN VACIO */
let accessToken = user ? 'Token '+ user.access_token : null;

/*
 *  CONFIGURACIÓN INICIAL DE LOS SERVICIOS REST
 */

var http = axios.create({
    
    baseURL: 'https://trodapp.com/api/',
    //baseURL: 'http://192.168.1.105:8000/api/',
    // baseURL: 'http://10.0.0.10:8000/api/',
    headers: {
        'Content-type': 'application/json',
        Authorization: accessToken
    }
});

export default http;