import axios from 'axios'
import Swal from 'sweetalert2'
import http from '@/assets/http-commom.js'

/* CLASE QUE LLAMA LOS SERVICIOS DE LA API */
class AuthService {
    /* INICIA SESIÓN, PASA POR PARAMETRO LOS DATOS DEL USUARIO */
    login(user) {
        return axios
            .post('user/login/', {
                username: user.username,
                password: user.password
            })
            .then(response => {

                /* 
                *   GUARDA TODOS LOS DATOS EN UN JSON DE MANERA LOCAL
                */

               localStorage.setItem('user', JSON.stringify(response.data));

                /* EN CASO CONTRARIO SOLO RETORNA LA RESPUESTA */

                return response.data;
            })
    }

    /* CIERRA SESIÓN */
    logout() {
        
        return http
            .get('user/logout/')
            .then(() => {
            
            /* ELIMINA LOS DATOS DEL USUARIO DEL JSON LOCAL */
            
                localStorage.removeItem('user');

                Swal.fire({
                    icon: 'success',
                    title: 'Logout successfully',
                }).then(()=> location.replace('/'));
            })
            .catch(error => {

                /* 
                *  SI HAY UN ERROR LO CAPTURA Y LO MUESTRA EN UNA MODAL
                */

                Swal.fire({
                    icon: 'error',
                    title: 'Error',
                    text: error.response.data['non_field_errors']
                });
    
            });
    }

    forgot_password(data){
        return axios
        .post('user/forgot_password/',{
            email: data
        })
    }

    check_token(pk){
        return axios
        .get('user/'+pk+'/forgot_password_challenge/')
        .then(res => {
            return res.data
        })
    }

    change_password(data){
        return axios
            .post('user/change_password/', {
                unique_id: data.unique_id,
                new_password: data.new_password,
                confirm_password: data.confirm_password,
            })
            .then(res => {
                return res.data
            })
    }

}

export default new AuthService();